import { defineComponent } from './factory';

defineComponent('app', {
  template: `
    <div id="furet-ui-app">
      <furet-ui-appbar />
      <notifications></notifications>
      <router-view></router-view>
    </div>`,
});

defineComponent('furet-ui-appbar', {
  template: `
    <header class="hero is-primary">
      <furet-ui-appbar-header />
      <furet-ui-appbar-body />
      <furet-ui-appbar-footer />
    </header>
  `,
});

defineComponent('furet-ui-appbar-header', {
  template: `
    <div class="hero-head">
      <nav class="nav">
        <div class="container">
          <div class="navbar-brand">
            <furet-ui-appbar-header-brand />
             <span class="navbar-burger burger" data-target="navbarMenuHeroA">
              <span></span>
              <span></span>
              <span></span>
            </span>
          </div>
        </div>
        <div id="navbarMenuHeroA" class="navbar-menu">
          <furet-ui-appbar-user-menu />
        </div>
      </nav>
    </div>
  `,
});

defineComponent('furet-ui-appbar-header-brand', {
  template: `
    <a class="navbar-item">
      <img v-bind:src="logo" alt="Logo">
    </a>
  `,
  prototype: {
    computed: {
      logo() {
        return `${process.env.API_REST_URL}furet-ui/logo`;
      },
    },
  },
});

defineComponent('furet-ui-appbar-body', {
  template: `
    <div class="hero-body">
      <div class="container has-text-centered">
        <h1 class="title">
          {{ title }}
        </h1>
      </div>
    </div>
  `,
  prototype: {
    computed: {
      title() {
        return this.$store.state.global.title;
      },
    },
  },
});

defineComponent('furet-ui-appbar-footer', {
  template: `
    <div class="hero-foot">
      <furet-ui-appbar-space-menu />
    </div>
  `,
});



