// import './picture';
import _ from 'underscore';
// import {dispatchAll} from './store';
// import {json_post} from './server-call';
import { defineComponent } from './factory';

export const selectCard = (type, router, store, card) => {
  const key = 'UPDATE_{0}_MENU'.format((type || '').toUpperCase());
  router.push({ name: card.homePageResource });
  store.commit(key, { value: { label: card.label, image: card.image } });
};

export const openMenu = (type, route, router, store) => {
  store.commit('UPDATE_PREVIOUS_PATH', { route });
  router.push({ name: 'menu', params: { type } });
};

export const closeMenu = (router, store) => {
  router.push({ path: store.state.global.previous_path });
};

defineComponent('furet-ui-menu', {
  template: `
    <div>
      <div class="level">
        <div class="level-item">
          <a class="button is-primary is-medium is-outlined"
              v-bind:style="{margin: '5px'}"
              v-on:click.stop="closeMenu"
          >{{$t('menus.close')}}</a>
        </div>
        <div class="level-item">
          <b-input
            type="search"
            icon-pack="fa"
            icon="search"
            v-bind:placeholder="$t('menus.search')"
            v-model="searchText">
          </b-input>
        </div>
      </div>
      <section 
        v-bind:style="{color: '#363636', padding: '5px'}"
      >
        <div class="columns is-multiline is-mobile">
          <div 
            class="column is-12-mobile is-half-tablet is-one-quarter-desktop"
            v-for="card in cards"
          >
            <article 
              class="box media furet-ui-space-menu" 
              v-on:click.stop="selectCard(card)"
              v-bind:style="{padding: '2px'}"
            >
              <aside class="media-left">
                <figure class="image is-32x32">
                  <furet-ui-picture
                    v-bind:type="card.image.type"
                    v-bind:value="card.image.value"
                  />
                </figure>
              </aside>
              <section class="media-content">
                <h1>{{card.label}}</h1>
                <p v-bind:style="{whiteSpace: 'pre-wrap'}">{{card.description}}</p>
              </section>
            </article>
          </div>
        </div>
      </section>
    </div>`,
  prototype: {
    props: ['type', 'unittest_search'],
    data() {
      return {
        searchText: this.unittest_search || '',
      };
    },
    computed: {
      cards() {
        const re = new RegExp(this.searchText, 'ig');
        // eslint-disable-next-line
        const stateName = this.type + 'menu';
        const store = this.$store.state[stateName];
        const cards = _.filter(store.values, c => (re.test(c.label) || re.test(c.description)));
        return cards;
      },
    },
    methods: {
      selectCard(card) {
        selectCard(this.type, this.$router, this.$store, card);
      },
      closeMenu() {
        closeMenu(this.$router, this.$store);
      },
    },
  },
});

defineComponent('furet-ui-appbar-menu', {
  template: `
    <a class="nav-item" v-if="hasValue">
      <a class="button" v-on:click="openMenu">
        <furet-ui-picture 
          v-bind:type="value.image.type" 
          v-bind:value="value.image.value" 
        />
        <span> {{ value.label }} </span>
      </a>
    </a>`,
  prototype: {
    props: ['type'],
    computed: {
      value() {
        // eslint-disable-next-line
        const stateName = this.type + 'menu';
        return this.$store.state[stateName].value;
      },
      hasValue() {
        // eslint-disable-next-line
        const stateName = this.type + 'menu';
        const value = this.$store.state[stateName].value;
        const hasImage = _.keys(value.image || {}).length > 0;
        const hasLabel = value.label.length > 0;
        return hasImage && hasLabel;
      },
    },
    methods: {
      openMenu() {
        openMenu(this.type, this.$route, this.$router, this.$store);
      },
    },
  },
});

defineComponent('furet-ui-appbar-left-menu', {
  template: '<furet-ui-appbar-menu type="left"/>',
});

defineComponent('furet-ui-appbar-right-menu', {
  template: '<furet-ui-appbar-menu type="right"/>',
});



